.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding-bottom: 15px;
    width: 75%;
    position: relative;
}

.gif img, .btn-link {
  position: absolute;
}

.btn-link {
  top: 50%;
  left: 5%;
}

.volume-icon {
  height: 100px;

  color: #5bc0de;
}

#louise-gif {
  max-height: 300px;
  top: 89.5%;
  right: 0;
}

#linda-gif {
  max-width: 200px;
  top: 72%;
  left: 0;
}

.incorrect {
  -webkit-animation: shake 0.82s;
          animation: shake 0.82s;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

.off {
  display: block;
}

.on {
  display: none;
}

/* .btn-link, .volume-icon {
  position: absolute;
  left: 5%;
} */
  
@-webkit-keyframes shake {
  10%, 90% {
    -webkit-transform: translate3d(-10px, 0, 0);
            transform: translate3d(-10px, 0, 0);
  }
  20%, 80% {
    -webkit-transform: translate3d(10px, 0, 0);
            transform: translate3d(10px, 0, 0);
  }
  30%, 50%, 70% {
    -webkit-transform: translate3d(-20px, 0, 0);
            transform: translate3d(-20px, 0, 0);
  }
  40%, 60% {
    -webkit-transform: translate3d(20px, 0, 0);
            transform: translate3d(20px, 0, 0);
  }
}
  
@keyframes shake {
  10%, 90% {
    -webkit-transform: translate3d(-10px, 0, 0);
            transform: translate3d(-10px, 0, 0);
  }
  20%, 80% {
    -webkit-transform: translate3d(10px, 0, 0);
            transform: translate3d(10px, 0, 0);
  }
  30%, 50%, 70% {
    -webkit-transform: translate3d(-20px, 0, 0);
            transform: translate3d(-20px, 0, 0);
  }
  40%, 60% {
    -webkit-transform: translate3d(20px, 0, 0);
            transform: translate3d(20px, 0, 0);
  }
}

.incorrect img{
  display:none;
}

@media only screen and (max-width: 1300px) {
  .gif {
    display: none;
  }
}


